@keyframes home_ani {
  from {
    color: rgba(34, 31, 194, 0);
    margin-top: 60px;
  }
  to {
    color: rgba(34, 31, 194, 1);
    margin-top: 0px;
  }
}

.Home_container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  background-color: white;
  height: 100vh;
  overflow: hidden;
}
.Home_desc {
  display: flex;
  flex-direction: column;
  font-family: "Titillium Web", "san-selif";
  font-weight: 900;
  margin-left: 80px;
  margin-top: 80px;
  font-size: 100px;
  animation: home_ani 1s ease-out forwards alternate;
  color: rgba(34, 31, 194, 1);
}

/* mobile */

.bar_Home_desc {
  display: flex;
  flex-direction: column;
  font-family: "Titillium Web", "san-selif";
  font-weight: 900;
  margin-left: 40px;
  margin-right: 40px;
  margin-top: 40px;
  font-size: 50px;
  width: 100%;
  animation: home_ani 1.25s ease forwards alternate;
  color: rgba(34, 31, 194, 1);
}
